.footer {
  font-size: 16px;
  background-color: $white;
  @include sm {
    font-size: 14px; }
  &__inner {
    @include flex(space-between, center, row, wrap);
    padding-top: 30px;
    padding-bottom: 30px;
    @include lg {
      padding-top: 15px;
      padding-bottom: 15px; }
    @include md {
      padding-top: 10px;
      padding-bottom: 10px; }
    @include sm {
      justify-content: center; } }
  &__copy {
    color: $gray-dark;
    flex-grow: 1;
    text-align: center;
    @include sm {
      width: 100%;
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__logo {
    width: 150px;
    @include sm {
      width: auto; } }
  &__btn {
    width: 150px;
    text-align: right;
    @include sm {
      width: auto;
      text-align: center; } } }
